#home{
    height: auto;
    width: 100%;
    /* border: 1px solid red; */
    padding-top: 100px; 
   padding-bottom: 10px;
}


#homediv{
    display: grid;


    width: 90%;
  grid-template-columns: repeat(2,1fr);
  align-content: center;
    /* border: 1px solid white; */
    margin: auto;

}

#homediv>div{
    width: auto;
    width: 100%;
    /* border: 1px solid rgb(255, 255, 255); */
}
#homeresume{
  color:"#2e9b56";
  width:"150px";
  margin:"auto";
  padding:"9px";
  margin-top:"15px";
  margin-bottom:"15px";
  border-radius:"5px";
  border:"2px solid #2e9b56";
}
#homeresume:hover{
   background: #2e9b56;
   color: white;

}
@media all and (min-width:579px ) and (max-width:880px){
    
#homediv{
   
  grid-template-columns: repeat(2,1fr);

}
}

@media all and (min-width:140px ) and (max-width:578px){
    #home{
        height: auto;
     
       
    }
    #homediv{
       
      grid-template-columns: repeat(1,1fr);
    
    }
    }