#contact{
     width: 160px;
     margin: auto;
     display: flex;
     padding-top: 25px;
     padding-bottom: 25px;
     background-color: rgb(0, 0, 0);
     padding-left: "auto";
     padding-right: "auto";
}

#divoftag1{
    border-radius: 10px;
    display: flex;
    /* border: 3px solid #2e9b56; */
    /* background-color: rgb(255, 255, 255); */
    color: rgb(55, 93, 177);
    padding: 5px;
width: 150px;
margin: 8px;
}
#divoftag1:hover{
    /* position:relative;
    width:170px */
    border: 1px solid #2e9b56;
}
#divoftag2{
    border-radius: 10px;
    display: flex;
    /* border: 3px solid #2e9b56; */
    background-color: rgb(15, 15, 15);
    color: white;
    padding: 5px;
width: 150px;
margin: 8px;
}
