    border: 1px solid #2e9b56;
    /* position:relative;
    width:170px */
}
#contactMain{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
@media all and (min-width:549px ) and (max-width:880px){
    #contact{
        width: 200px;
        margin: auto;
   }
   #contactMain{
    display: grid;
    grid-template-columns: 1fr ;
}
}
